  ngOnInit() {

    this.id = this.route.snapshot.paramMap.get("id");
    this.ref=this.route.snapshot.queryParams.reference;
    console.log(this.ref)
    this.FilterForm = this.fb.group({
      package_id: [''],
      start: [''],
      end: [''],
      status: ['1'],
      email: [''],
      phone_no: [''],

    })


    this.p = 1
    this.pageChanged(1);
  }



  pageChanged(newPage: any) {

    this.p = newPage

    this.getDataWithID(newPage, this.pageSize,this.id,this.FilterForm.value['package_id'], this.FilterForm.value['start'], this.FilterForm.value['end'], this.FilterForm.value['status'], this.FilterForm.value['email'], this.FilterForm.value['phone_no'])

  }


  transactionSubmit() {

    this.p = 1

    this.getDataWithID(this.p, this.pageSize, this.id,this.FilterForm.value['package_id'], this.FilterForm.value['start'], this.FilterForm.value['end'], this.FilterForm.value['status'], this.FilterForm.value['email'], this.FilterForm.value['phone_no'])


  }


  getDataWithID(pageNumber, limit, page_id, ref, start, end, status, email, phone) {
    let condition = ''
    if (page_id) {
      condition = condition + '&page_id=' + page_id
    }

    if (ref) {
      condition = condition + '&ref_no=' + ref
    }


    if (start && end) {
      condition = condition + '&start_date=' + moment(start).format('YYYY-MM-DD HH:mm:ss') + '&end_date=' + moment(end).format('YYYY-MM-DD HH:mm:ss')
    }

    if (status) {
      condition = condition + '&status=' + status
    }

    if (email) {
      condition = condition + '&email=' + email
    }

    if (phone) {
      condition = condition + '&phone=' + phone
    }
    this.transaction_service.getPageHistory(pageNumber, limit, condition).then(
      res => {
        this.is_loading = false
        
        if (res['message'] == "success") {
          this.transaction_list = res['data']
        console.log(this.transaction_list)
          //console.log(JSON.parse(JSON.stringify(this.repairJson(this.transaction_list.additional_data), null, 2)))
 
          this.total = res['data']['limit'];
        }
        else {
          this.userMessage = res['message']
          this.toastr.showToast('danger', 'error', this.userMessage)
        }
      },
      err => {
        this.is_loading = false
        console.log(err);
        this.toastr.showToast('danger', 'error', 'Internal Server Error')
      }
    );


  }

