import { Component, OnInit, TemplateRef, ViewChild, ElementRef, ChangeDetectorRef, Input } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormArray, FormControl } from '@angular/forms';
import { NbDialogService } from '@nebular/theme';
import { ToastrComponent } from '../../../@core/components';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { PaymentPageService } from '../../../@core/services/app_services/payment-page.service';
// import * as ClassicEditor from 'ckeditor';
import * as ClassicEditor from '@ckeditor/ckeditor5-build-classic';
