import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { PaymentPageComponent } from "./payment-page.component";
import { CreatePageComponent } from "./create-page/create-page.component";
import { CreateItemComponent } from "./create-item/create-item.component";
import { PageHistoryComponent } from "./page-history/page-history.component";
import { PrivillageAccess } from "../../@core/services/_privillageguard.service";
import {
  PAYMENT_LINK,
  PAYMENT_PAGE,
  DASHBOARD,
  TRANSACTION_HISTORY,
  REPORTS,
  CUSTOMERS,
  PAYOUT,
  ACC_SETTING,
  DEV_CONFIGURATION,
  REFUNDS,
} from "../../../constant";

const routes: Routes = [
  {
    path: "",
    component: PaymentPageComponent,
    children: [
      {
        path: "payment-page-create",
        component: CreatePageComponent,
        canActivate: [PrivillageAccess],
        data: {
          pri: PAYMENT_PAGE,
        },
      },
      {
        path: "payment-page-items",
        component: CreateItemComponent,
        canActivate: [PrivillageAccess],
        data: {
          pri: PAYMENT_PAGE,
        },
      },
      {
        path: "page-history/:id",
        component: PageHistoryComponent,
        canActivate: [PrivillageAccess],
        data: {
          pri: PAYMENT_PAGE,
        },
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class PaymentPageRoutingModule {}
