import { Component, OnInit, ViewChild,ElementRef, ChangeDetectorRef } from '@angular/core';
import { PaymentPageService } from '../../../@core/services/app_services/payment-page.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NbDialogService } from '@nebular/theme';
import { environment } from '../../../../environments/environment';
import { ToastrComponent } from '../../../@core/components';
import { ModalDirective } from 'ngx-bootstrap/modal';

@Component({
  selector: 'ngx-create-item',
  templateUrl: './create-item.component.html',
  styleUrls: ['./create-item.component.scss']
})
export class CreateItemComponent implements OnInit {

  @ViewChild('dialogAddCollection', { static: false }) public dialogAddCollection: ModalDirective;
  @ViewChild('dialogUpdateCollection', { static: false }) public dialogUpdateCollection: ModalDirective;
  @ViewChild('dialogDeleteCollection', { static: false }) public dialogDeleteCollection: ModalDirective;
  @ViewChild('dialogViewCollection', { static: false }) public dialogViewCollection: ModalDirective;
  @ViewChild('imageInput', { static: false }) fileUploader: ElementRef;
  
  addCollectionItemForm: FormGroup;
  updateCollectionItemForm: FormGroup;
  deleteCollectionItemForm: FormGroup;
  
  collection_add_model
  item = false
  image: any;
  imageBase64: any
  image_base_path = environment.aws;
  is_loading = false
  loadingLargeGroup = false
  userMessage

  p: number = 1;
  total: number = 0;
  pageSize: number = 10;
  batch_details = [] = []
  search_by = ""
  filter_collection_data = []
  filter_data = []
  search_text=""
  count=0
  is_service =""
  key_word =""
  item_data : any = []
 


  constructor(
    private dialogService: NbDialogService,
    private toastr: ToastrComponent,
    private fb: FormBuilder,
    private cd: ChangeDetectorRef,
    private paymentPageService: PaymentPageService,
  ) {
    this.addCollectionItemForm = this.fb.group({
      item_name: ["", Validators.required],
      // is_service: ["", Validators.required],
      description: ["", Validators.required],
      gross_amount: ["", Validators.required],
      discounts: [""],
      on_image_caption: [""],
      image: [""],
      currency:["", Validators.required],

    });
    this.updateCollectionItemForm = this.fb.group({
      item_name: ["", Validators.required],
      // is_service: ["", Validators.required],
      description: ["", Validators.required],
      gross_amount: ["", Validators.required],
      discounts: [""],
      on_image_caption: [""],
      image: [""],
      id:[""],
      currency:["", Validators.required],

      

    });
    this.deleteCollectionItemForm = this.fb.group({
      id:[""]

    });
  }

  ngOnInit() {
    this.getCollectionData(1, this.pageSize,'')
  }

  openAddCollection() {
    this.dialogAddCollection.show();
  }
  closeAddCollection() {
    this.dialogAddCollection.hide();
    this.addCollectionItemForm.reset()
    this.item = false

  }
  chooseService(event) {
    console.log(event)
    if (event == "true") {
      this.item = false
      this.onRemoveValidationClickImageCaption()

    } else {
      this.item = true
      this.onAddValidationClickImageCaption()
    }
  }
  onAddValidationClickImageCaption() {
    this.addCollectionItemForm.controls["on_image_caption"].setValidators(Validators.required);
    this.addCollectionItemForm.controls["on_image_caption"].updateValueAndValidity();
    this.addCollectionItemForm.controls["image"].setValidators(Validators.required);
    this.addCollectionItemForm.controls["image"].updateValueAndValidity();
  }

  onRemoveValidationClickImageCaption() {
    this.addCollectionItemForm.controls["on_image_caption"].clearValidators();
    this.addCollectionItemForm.controls["on_image_caption"].updateValueAndValidity();
    this.addCollectionItemForm.controls["image"].clearValidators();
    this.addCollectionItemForm.controls["image"].updateValueAndValidity();
  }
  // image upload///////////////////////////////////////////////////////////////////
  onFileChange(event) {
    console.log(event)
    let reader = new FileReader();

    if (event.target.files && event.target.files.length) {
      const [file] = event.target.files;
      reader.readAsDataURL(file);

      reader.onload = () => {
        this.imageBase64 = reader.result;
        this.image = reader.result;
        this.cd.markForCheck();
      };
    }
  }
  updateValues(event) {

  }

  resetImage() {
    this.image = ""
    this.imageBase64 = ""
    // console.log(this.fileUploader.nativeElement, 'rest')
    // this.fileUploader.nativeElement.value = "";
  }
  // genarate payment page link
  addCollectionItemData() {
    this.loadingLargeGroup = true
    // console.log(this.addCollectionItemForm.get('is_service').value,'55')
    let data = {
      "item_name": this.addCollectionItemForm.value["item_name"],
      "is_service":1,
      "currency":this.addCollectionItemForm.value["currency"],
      "description": this.addCollectionItemForm.value["description"],
      "gross_amount": this.addCollectionItemForm.value["gross_amount"],
      "discounts": this.addCollectionItemForm.value["discounts"] ? this.addCollectionItemForm.value["discounts"] : 0.00,
    }
    // if (this.addCollectionItemForm.get('is_service').value == "false") {
    //   data["on_image_caption"] = this.addCollectionItemForm.get('on_image_caption').value
    //   data["image"] = [this.imageBase64]
    // }
    console.log(data, 'test data')

    this.paymentPageService.postPaymentItem(data).then(res => {
      this.loadingLargeGroup = false
      // console.log(res)
      if (res['status'] == 100) {
        this.getCollectionData(1, this.pageSize,'')
        this.closeAddCollection()
        this.toastr.showToast('success', 'Success', 'Item added successfully')


      } else {
        
        this.userMessage = res['message']
        this.toastr.showToast('danger', 'Error', this.userMessage)

      }
    }, error => {
      this.loadingLargeGroup = false
      console.log(error)
      this.toastr.showToast('danger', 'Error', 'Internal server error')
    })
  }
  //get collections items
  
  pageChangedCouponHistory(newPage: any){
    this.p = newPage
    this.getCollectionData(newPage, this.pageSize);
  }
  filter_dataCollection(){
    this.p = 1
    this.getCollectionData(this.p,this.pageSize, this.search_by, this.search_text)
  }
  clear_data(){
    this.search_by = ""
    this.search_text = ""
    this.getCollectionData(1,this.pageSize, "", "")
  }

  getCollectionData(p, pageSize,is_service="", key_word="") {
    this.is_loading = true
    let params = "page=" + p + "&limit=" + pageSize

    if(is_service) {
      params+="&is_service=" + is_service 
    }
    if(key_word) {
      params+="&key_word=" + key_word 
    }
   
    this.paymentPageService.getCollectionBySearch(params).then(
      res => {
        this.is_loading = false
        if (res['status'] == 100) {
          this.filter_collection_data = res['data'];
          this.total = res['count'];
        }
        else {

          this.userMessage = res['message']
          this.toastr.showToast('danger', 'Error', this.userMessage)
        }
      },
      err => {
        this.is_loading = false
        console.log(err);
        this.toastr.showToast('danger', 'Error', 'Internal server error')
      }
    );
  }
  //update collection data
  openUpdateCollection(collection) {
    if(collection.is_service == '1'){
      console.log('service')
      this.onRemoveValidationClickImageCaption()
      setTimeout(() => {
        
        this.updateCollectionItemForm.setValue({
          id:collection.id,
          item_name:collection.item_name,
          // is_service:(collection.is_service ==1?'true':'false'),
          description:collection.description,
          gross_amount:collection.gross_amount,
          discounts:collection.discounts,
          on_image_caption:'',
          image:'',
          currency:collection.currency

        });
      }, 1);
    }
    // else if(collection.is_service =='0'){
    //   console.log('not service')
    //   this.onAddValidationClickImageCaption()
    //   setTimeout(() => {
    //     this.updateCollectionItemForm.setValue({
    //       id:collection.id,
    //       item_name:collection.item_name,
    //       is_service:(collection.is_service ==1? 'true':'false'),
    //       description:collection.description,
    //       gross_amount:collection.gross_amount,
    //       discounts:collection.discounts,
    //       on_image_caption:collection.on_image_caption,
    //       image:collection.image,
    //     });
    //   }, 1);
    //   this.image = this.image_base_path + collection.image

    // }

    this.dialogUpdateCollection.show();
   

    }
  closeUpdateCollection() {
    this.dialogUpdateCollection.hide();
    this.updateCollectionItemForm.reset()
    this.resetImage()
    this.item = false

    setTimeout(() => {
      this.loadingLargeGroup = false
    }, 500);

  }

  updateCollectionItemData() {
    this.loadingLargeGroup = true
   
    let data = {
      "item_name": this.updateCollectionItemForm.value["item_name"],
      // "is_service": ((this.updateCollectionItemForm.value['is_service'] == 'true') ? 1 : 0),
      "is_service": 1,
      "description": this.updateCollectionItemForm.value["description"],
      "gross_amount": this.updateCollectionItemForm.value["gross_amount"],
      "discounts": this.updateCollectionItemForm.value["discounts"] ? this.updateCollectionItemForm.value["discounts"] : 0.00,
      "currency": this.updateCollectionItemForm.value["currency"],

    }
    // if (this.updateCollectionItemForm.get('is_service').value == "false" && this.imageBase64) {
    //   data["on_image_caption"] = this.updateCollectionItemForm.get('on_image_caption').value
    //   data["image"] = [this.imageBase64]
    // }
    // if (this.updateCollectionItemForm.get('is_service').value == "false" && !this.imageBase64) {
    //   data["on_image_caption"] = this.updateCollectionItemForm.get('on_image_caption').value
    // }
    console.log(data, 'test data')

    this.paymentPageService.updateCollection(this.updateCollectionItemForm.get('id').value,data).then(res => {
      // console.log(res)
      this.closeUpdateCollection()

      if (res['status'] == 100) {
        this.getCollectionData(1, this.pageSize,'')
        this.toastr.showToast('success', 'Success', 'Item updated successfully')
      } else {  
        this.userMessage = res['message']
        this.toastr.showToast('danger', 'Error', this.userMessage)
      }
    }, error => {
      this.closeUpdateCollection()
      console.log(error)
      this.toastr.showToast('danger', 'Error', 'Internal server error')
    })
  }
  //delete collection item
  openDeleteCollection(collection) {
    setTimeout(() => {
      this.deleteCollectionItemForm.setValue({
        id:collection.id,
      });
    }, 1);
    this.dialogDeleteCollection.show();
  }
  closeDeleteCollection() {
    this.dialogDeleteCollection.hide();
    this.deleteCollectionItemForm.reset()
    setTimeout(() => {
      this.loadingLargeGroup = false;
    }, 500)
    
  }
  deleteCollectionItemData() {
    this.loadingLargeGroup = true;

    this.paymentPageService.deleteCollectionData(this.deleteCollectionItemForm.get('id').value).then(res => {
      // console.log(res)
      this.closeDeleteCollection()

      if (res['status'] == 100) {
        this.getCollectionData(1, this.pageSize,'')
        this.toastr.showToast('success', 'Success', 'Item deleted successfully')
      } else {
        this.userMessage = res['message']
        this.toastr.showToast('danger', 'Error', this.userMessage)
      }
    }, error => {
      this.closeDeleteCollection()
      console.log(error)
      this.toastr.showToast('danger', 'Error', 'Internal server error')
    })
  }
  //view collection item details
  openViewCollection(collection) {
    this.item_data = collection
    this.dialogViewCollection.show();
    this.image = this.image_base_path + collection.image
  }
  closeViewCollection() {
    this.dialogViewCollection.hide()
  }
  toggleLoadingLargeGroupAnimation() {
    this.loadingLargeGroup = true;

  }



}
